import React from "react";
import "./Detail.css";

class Detail extends React.Component {
  componentDidMount() {
    const { location, history } = this.props;
    if (location.state === undefined) {
      history.push("/");  //주소 변경
    }
  }
  render() {
    const { location } = this.props;
    if (location.state) {
      const state = location.state;
      return (
      <div className="datail__form">
        <div className="detail__img"><img src={state.poster} alt={state.title} title={state.title} /></div>
        <div className="detail__contents">
          <h2>{state.title}</h2>
          <h3 className="detail__year">{state.year}</h3>
          <ul className="detail__genres">
            {state.genres.map((genre, index) => (
            <li key={index} className="detail__genre">
                GENRE: {genre}
            </li>
            ))}
          </ul> 
          <p className="detail__summary">{state.summary}</p>
        </div>
      </div>
     );
    } else {
      return null;
    }
  }
}
export default Detail;